import './App.css';
import { useState, useEffect, useLayoutEffect } from 'react';
import AceEditor from "react-ace";
import "ace-builds/src-noconflict/mode-typescript";
import "ace-builds/src-noconflict/theme-monokai";
import Box from '@mui/material/Box';
import Drawer from '@mui/material/Drawer';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import List from '@mui/material/List';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import Modal from '@mui/material/Modal';
import { Button } from '@mui/material';
import { createTheme } from '@mui/material';
import { ThemeProvider } from '@mui/material';
import { codePlaceholder } from './data/code';
import styled from '@emotion/styled';
import { Demo } from './components/Demo';

const hoverStyle = {
		'&:hover': {
			backgroundColor: '#303129',
		}
};

const textStyle = {
	color: '#FFFFFF'
};

const StyledButton = styled(Button)(hoverStyle);
const StyledListItem = styled(ListItem)(hoverStyle);
const StyledTypography = styled(Typography)(textStyle);

const drawerWidth = 300;
const theme = createTheme({
	palette: {
		text: {
			primary: '#FFFFFF'
		},
		primary: {
			main: '#303129',
			light: '#2D2A2E',
			dark: '#202020'
		},
	}
});

const demoItems = [
	{
		class: '.editor',
		text: `This is a heart of the app. 
		The Code Editor! 
		For this template the only thing you need to add is your actual Jira Cloud Issue key.`,
		width: '700px',
		height: '500px',
		textPosition: {
			top: '90%',
			left:'79%'
		},
		borderBadius: '50%'
	},
	{
		class: '.menu',
		text: `Here's the list of all your files for this template.
		Fear not! 
		Everything is prepared to run except for a couple of constants, but you always can add, edit or delete the files to make your own script.`,
		width: '500px',
		height: '500px',
		textPosition: {
			top: '37%',
			left:'131%'
		},
		borderBadius: '0%'
	},
	{
		class: '.console',
		text: `This is a console. 
		Useful thing to debug your code and print some data.`,
		width: '600px',
		height: '400px',
		textPosition: {
			top: '8%',
			left:'110%'
		},
		borderBadius: '0%'
	},
	{
		class: '.run-button',
		text: 'Click "Run" button when you are ready!',
		width: '200px',
		height: '200px',
		textPosition: {
			top: '114%',
			left:'-140%'
		},
		borderBadius: '50%'
	},
];

function App() {
	const [isDemoActive, setDemoActive] = useState(true); //to turn off demo mode
	const [currentDemoItem, setDemoItem] = useState(demoItems[0]);
	const [position, setPos] = useState();
	
	const handlePosition = (classArg) => {
		const button = document.querySelector(classArg);
		const pos = button.getBoundingClientRect();
        setPos([pos.x, pos.y]);
	};

	const handleNext = () => {
		const currentDemoIndex = demoItems.findIndex(item => item.class === currentDemoItem.class);
		let item;
		if (currentDemoIndex < demoItems.length - 1) {
			item = demoItems[currentDemoIndex + 1];
			setDemoItem(demoItems[currentDemoIndex + 1]);
			handlePosition(item.class);
		} else {
			item = demoItems[0];
			setDemoItem(demoItems[0]);
			handlePosition(item.class);
		}
	};

	useLayoutEffect(() => {
		const resizeCallback = (e) => {
			handlePosition(currentDemoItem.class)
		};
		resizeCallback();
		window.addEventListener('resize', resizeCallback);
		return () => window.removeEventListener('resize', resizeCallback);
	},[currentDemoItem.class]);

  return (
	<ThemeProvider theme={theme}> 
		<Box onClick={handleNext} sx={{ display: 'flex', bgcolor: 'primary.light', overflow: 'hidden' }}>
			
			{isDemoActive && <Demo 
			classProp={currentDemoItem.class} 
			position={position}
			text={currentDemoItem.text} 
			width={currentDemoItem.width} 
			height={currentDemoItem.height} 
			textPosition={currentDemoItem.textPosition}
			borderRadius={currentDemoItem.borderBadius}
			/>}

			<AppBar
			position="fixed"
			sx={{ width: `calc(100% - ${drawerWidth}px)`, ml: `${drawerWidth}px`, bgcolor: 'primary.light' }}
			>
				<Toolbar sx={{display: 'flex', justifyContent: 'space-between'}}>
					<Typography variant="h6" noWrap component="div">My Workspace</Typography>
				 	<StyledButton className='run-button' variant='contained' sx={{mr:5, bgcolor: 'primary.dark'}}>Run</StyledButton>
				</Toolbar>
			</AppBar>
			<Drawer
			sx={{
				width: drawerWidth,
				flexShrink: 0,
				'& .MuiDrawer-paper': {
					width: drawerWidth,
					boxSizing: 'border-box',
					bgcolor: 'primary.dark',
				},
				bgcolor: 'primary.light',
			}}
			variant="permanent"
			anchor="left"
			>
				<Toolbar sx={{bgcolor: 'primary.light'}}>
					<Typography variant="h6" noWrap component="div">Files</Typography>
				</Toolbar>
				<Divider />
				<List className='menu' sx={{bgcolor: 'primary.light',}}>
					{['script.ts', 'gubbins.ts', 'gubbins.ts', 'gubbins.ts', 'gubbins.ts', 'gubbins.ts', 'gubbins.ts', 'gubbins.ts',].map((text, index) => (
					<StyledListItem button key={index}>
						<ListItemText primary={text} />
					</StyledListItem>
					))}
				</List>
			</Drawer>

			<Box
			component="main"
			sx={{width: '100%', height: '100vh', flexGrow: 1, bgcolor: 'primary.dark'}}
			>
				<Toolbar />
				<AceEditor
				className='editor'
				width='100%'
				height='50%'
				mode="typescript"
				theme="monokai"
				fontSize={14}
				showPrintMargin={false}
				showGutter={true}
				highlightActiveLine={true}
				value={codePlaceholder}
				wrapEnabled={true}
				setOptions={{
					enableBasicAutocompletion: false,
					enableLiveAutocompletion: false,
					enableSnippets: true,
					showLineNumbers: true,
					tabSize: 4,
				}}/>
				<Toolbar sx={{display: 'flex', justifyContent: 'space-between'}}>
					<StyledTypography variant="h6" noWrap component="div">Console</StyledTypography>
				</Toolbar>	
				<Box
				className='console'
				component="main"
				sx={{width: '100%', height: '50%', flexGrow: 1, bgcolor: 'primary.light'}}
				></Box>	
			</Box>
		</Box>
	</ThemeProvider>
  );
}

export default App;
